import myCheckboxGroup from "@/components/myCheckboxGroupPopCause.vue";
import myPickerPart from "@/components/myPickerPartProps.vue";
import uniIcon from "@/components/uni-icons/uni-icons.vue";
let _this;
export default {
	components:{
		myPickerPart,
		myCheckboxGroup
	},
	data() {
		return {
			level:[],
			companyCode:'',
			statusCode:''
			
		}
	},
	onLoad:function(options){
		_this = this
	    this.companyCode = options.companyCode
	    this.statusCode = options.statusCode
		this.getCompanyLevel()
	},
	methods:{
		async getCompanyLevel(){
			const res = await this.$http.get('/choose/company_level',{})
			console.log('companyLevel',res)
			this.level = res.data.data.list
		},
		
		pickerChange:function(label,event){
		  console.log(event)
		  switch (label){
			  case 'customLevel':
				 this.statusCode = event
				 
				 break;
			}
			
		},
		
		async submit(){
			
			let data={
				companyCodeList:[this.companyCode],    //公司编码数组
				companyLevel: this.statusCode	     //等级
			}

			uni.showLoading({
				title: '提交中',
				mask: true
			});
			const res = await this.$http.post('/cm/update_level',{data:data})
			uni.hideLoading()
			uni.navigateBack({
				delta: -1
			});
		}
	}
}
